@font-face {
  font-family: 'GangwonEdu_OTFBoldA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'GangwonEdu_OTFBoldA';
  transition: .4s;
}

header {
  padding: 1.5rem 1rem;
  text-align: center;
  font-weight: bold;
  background-color: #24292f;
  color: #eee;
}


button {
  margin: 0.5rem;
}

h1 {
  font-size: clamp(1.75rem, 2.5vw, 3rem);
}

h2 {
  font-size: clamp(1.25rem, 2.5vw, 1.5rem);
  margin: 0 auto;
  width: 70%;
  padding: 0.75rem 0 0.5rem;
  text-align: center;
  border-bottom: 1px solid #24292f;
}

.list {
  padding: 20px 10px;
  border-bottom: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
  font-size: clamp(1.25rem, 2.5vw, 1.5rem);
}

.checkbox {
  display: flex;
  align-self: center;
  border: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin-right: 0.5rem;
  cursor: pointer;
}

input:checked+.listname {
  text-decoration: line-through;
}

.listname {
  width: 60%;
  color: #24292f;
  display: flex;
  align-self: center;
}

.today {
  margin: 0.5rem 0;
  font-size: clamp(0.75rem, 1.5vw, 1.25rem);
}

.good {
  width: 35%;
  display: flex;
  justify-content: end;
  align-items: center;
}

.good>span {
  cursor: pointer;
  margin: 0 0.3rem;
}

.good>button {
  border: none;
  background-color: transparent;
}

.date {
  width: 100%;
  font-size: clamp(0.9rem, 2.5vw, 1rem);
  padding-top: 10px;
}

.modal {
  padding: 20px;
  position: absolute;
  width: 300px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: royalblue;
  color: #eee;
  text-align: center;
}

.p-modal {
  background: linear-gradient(to right, #d66d75, #e29587);
  color: #eee;
  text-align: end;
  padding: 25px;
  position: absolute;
  top: 220px;
  right: 0;
  width: 40%;
}

.input {
  margin-top: 1rem;
  caret-color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-list {
  border: none;
  border-bottom: 1px solid aliceblue;
  width: 40%;
  background-color: transparent;
  outline: none;
  color: white;
}

.input-Button {
  margin: 0 0.5rem;
  width: 15px;
  height: 15px;
  font-size: 1rem;
  border: none;
  cursor: pointer;
}

.delete {
  margin: 0 0.4rem;
  border: 1px solid red;
  background-image: url(./delete.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
}

.cat {
  width: clamp(15%, 1vw, 10%);
  padding-bottom: clamp(15%, 1vw, 10%);
  border-radius: 50%;
  background-image: url(./cat.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  position: absolute;
  bottom: 50px;
  right: 50px;
  cursor: pointer;
}

.cat:hover {
  background-image: url(./hover_cat.jpg);
}